import { useState } from 'react';
import HTMLFlipBook from 'react-pageflip';
// import { Document, Page } from 'react-pdf';
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack';

import '../Modal.css';

function Journal(props) {
    const [numPages, setNumPages] = useState(null);
    const [pageNumber, setPageNumber] = useState(1);

    function onDocumentLoadSuccess({ numPages }) {
      setNumPages(numPages);
    }
    function pagesList(){
      var pages = [];
      for(var i=1; i<=numPages; i++){
        pages.push(
        <div style={{marginTop: '100px'}}>
          <div style={{marginTop: '100px'}}>
          <div style={{ width: '50%', float: 'left'}}>
          <Page width={1000} pageNumber={i} />
          </div>
          <div style={{ width: '50%', float: 'left' }}>
          </div>
            <Page
              width={1000}
              pageNumber={i}
              renderAnnotationLayer={false} // Disable annotation layer for the right half
              renderInteractiveForms={false} // Disable interactive forms for the right half
              renderTextLayer={false} // Disable text layer for the right half
              canvasWrapperStyle={{ overflow: 'hidden' }} // Clip content to half page
              viewport={{ // Adjust viewport to show only the right half
                rotation: 0,
                scale: 1,
                offsetX: -500,
                offsetY: 0,
                dontFlip: false,
              }}
            />
          </div>
        </div>
      );
      }
      return pages;
    }

    return (
        <Document
        file="./YogaJournal3.pdf"
        onLoadSuccess={onDocumentLoadSuccess}
        className='modal-90w'
        >        
        {/* <HTMLFlipBook width={500} height={707}> */}
        <HTMLFlipBook width={1024} height={768}>

            {pagesList()}
        </HTMLFlipBook>
        </Document>

    );
}
export default Journal;